.coretech-section{position:relative;width:100%;overflow:hidden;scroll-snap-align:start;}
.coretech-video-bg{position:absolute;inset:0;width:100%;height:100%;object-fit:cover;filter:brightness(0.3);z-index:0;}
.coretech-inner{position:relative;z-index:1;max-width:1200px;margin:0 auto;padding:6rem 1.5rem 5rem;text-align:center;color:#fafafa;}
.coretech-title{font-size:clamp(2.4rem,5vw,3.4rem);font-weight:900;letter-spacing:0.02em;margin-bottom:1.25rem;}
.coretech-sub{font-size:clamp(1.25rem,2.5vw,1.6rem);font-weight:500;opacity:0.9;margin-bottom:3rem;}
.coretech-grid{display:grid;gap:1.5rem;grid-template-columns:repeat(auto-fit,minmax(320px,1fr));margin-bottom:2.5rem;}
.coretech-grid img{width:100%;height:300px;object-fit:cover;border-radius:16px;box-shadow:0 0 25px rgba(0,255,247,0.12);}
.coretech-list{max-width:900px;margin:0 auto;font-size:1.2rem;line-height:1.95;text-align:left;list-style:none;padding:0;}
.coretech-list li{position:relative;margin-bottom:1.2rem;padding-left:1.6rem;}
.coretech-list li::before{content:'';position:absolute;left:0;top:0.7em;width:0.55rem;height:0.55rem;border-radius:50%;background:#00fff7;}